import { Divider, Typography } from "@mui/material";
import { Container } from "@mui/system";
import React, { useEffect, useState } from "react";
import { CartState } from "../../../Context/UseContext";
import Qvevri_free from "../../../Icons/Qvevri-free.jpg";
import "../Georgie.css";
import Breadcrumbs from "@mui/material/Breadcrumbs";
import ArrowForwardIosRoundedIcon from "@mui/icons-material/ArrowForwardIosRounded";
import { NavLink } from "react-router-dom";

function Qvevri() {
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);
  }, []);
  const {
    state: { theme },
  } = CartState();

  function handleClick(event) {
    event.preventDefault();
  }
  return (
    <>
      {loading ? (
        <div style={{ backgroundColor: theme === "light" ? "#fff" : "#000" }}>
          <div className="Cropped_Vin_de_Georgie">
            <Container>
              <h1 style={{ color: theme === "light" ? "#fff" : "#980433" }}>
                Qvevri
              </h1>
            </Container>
          </div>
          <Container style={{ padding: "50px 0" }}>
            <div
              style={{ padding: "0 0 50px 0" }}
              role="presentation"
              onClick={handleClick}
            >
              <Breadcrumbs
                separator={
                  <ArrowForwardIosRoundedIcon
                    style={{
                      fontSize: "15px",
                      color: theme === "light" ? "#000" : "#fff",
                    }}
                  />
                }
                aria-label="breadcrumb"
              >
                <NavLink
                  style={{
                    color: theme === "light" ? "#000" : "#fff",
                    textDecoration: "underline",
                  }}
                  to="/GEORGIE"
                >
                  GEORGIE
                </NavLink>
                <Typography
                  color={theme === "light" ? "#000" : "#fff"}
                  aria-current="page"
                >
                  QVEVRI
                </Typography>
              </Breadcrumbs>
            </div>
            <div className="georgieDescription">
              <h1 style={{ textAlign: "center", marginBottom: "40px" }}>
                Qvevri : lamphore traditionnelle du vin de Gorgie
              </h1>
              <br />
              <p>
                Les Qvevri ou Kvevri ( en gorgien) sont de grandes
                amphores ou jarres en argile  lextrmit conique. Leur
                contenance varie de 1000  6000 litres. Elles sont utilises
                pour la vinification et le stockage du vin de Gorgie, et sont
                la plus vieille mthode de vinification connue (des amphores
                anciennes de plus de 8000 ans ont t retrouves sur diffrents
                sites archologiques de Gorgie). Elles ont un impact trs
                positif sur le vin et confrent ses caractristiques uniques au
                vin de Gorgie.
              </p>
              <br />
              <p>
                En 2013, lUNESCO a inscrit la mthode viticole traditionnelle
                gorgienne en Qvevri au patrimoine culturel immatriel.
              </p>
              <br />
              <p>
                La composition des Qvevri permet de renforcer leurs parois mais
                joue galement un rle dantiseptique naturel. Leur intrieur
                est tapiss dune couche de cire dabeille qui permet dassurer
                ltanchit.
              </p>
            </div>
            <Container
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginTop: "50px",
                maxWidth: "640px",
              }}
            >
              <img
                style={{ alignSelf: "center", width: "100%" }}
                alt="Qvevri_free"
                src={Qvevri_free}
              />
            </Container>
            <Divider
              style={{ margin: "100px 0", border: "1px solid rgb(51, 51, 51)" }}
            ></Divider>
          </Container>
          <Container style={{ padding: "0 0 50px 0" }}>
            <div className="georgieDescription">
              <h1 style={{ textAlign: "center", marginBottom: "40px" }}>
                La vinification en Qvevri
              </h1>
              <br />
              <p>
                La mthode de{" "}
                <span style={{ fontWeight: "bold" }}>
                  vinification en Qvevri
                </span>{" "}
                consiste  fouler les raisins dans un fouloir do le jus
                scoule directement dans la Qvevri. Les peaux et les grappes
                non coules sont rcupres et ajoutes dans lamphore avec le
                jus. La fermentation alcoolique dmarre spontanment et dure une
                quinzaine de jours. Les Qvevri sont ensuite fermes
                hermtiquement avec un couvercle en bois ou en pierre selon les
                rgions (parfois aussi depuis peu en verre chez certains
                vignerons, car cela leur permet de surveiller la macration 
                travers le couvercle).
              </p>
              <br />
              <p>
                La fabrication des Qvevri prend deux  trois mois. Elles sont
                dans un premier temps faonnes et sches avant dtre places
                dans un four spcial o commence la cruciale tape de
                combustion.
              </p>
              <br />
              <p>
                Le fait denterrer les Qvevri permet au vin de toujours y garder
                une temprature constante denviron 13  15 degrs, idale pour
                la fermentation. Les processus chimiques de vinification sy
                droulent de manire lente et naturelle, et ne ncessitent aucun
                des quipements spciaux ou des additifs utiliss lors de la
                production industrielle du vin.
              </p>
              <br />
              <p>
                 Kakheti, lune des principales rgions viticoles de Gorgie,
                les Qvevri sont enterres dans des caves  vin appeles Marani.
                Certaines dentre elles ont obtenu le statut de muse en raison
                de leur anciennet et de leur caractre unique. Dans louest de
                la Gorgie, elles sont enterres  lair libre. Les techniques
                de vinification diffrent galement selon les rgions de
                Gorgie.
              </p>
              <br />
              <p>
                Presque tous les viticulteurs gorgiens produisent encore le vin
                en Qvevri. LAgence nationale du vin du ministre de
                lAgriculture de Gorgie les soutient et leur donne la
                possibilit de prsenter leurs produits dans les expositions
                internationales de vins naturels, ce qui permet daccroitre la
                notorit du vin de Gorgie vinifi en Qvevri  travers le
                monde.
              </p>
            </div>
            <Container
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginTop: "50px",
              }}
            >
              <img
                style={{
                  alignSelf: "center",
                  width: "100%",
                }}
                alt="Qvevri_free"
                src="https://i0.wp.com/www.bonumvinum.eu/wp-content/uploads/image-3.png?w=870"
              />
            </Container>
          </Container>
        </div>
      ) : null}
    </>
  );
}

export default Qvevri;
